.UploadImageMenu__Logo {
  height: 40vh;
  display: block;
  margin-top: 5vh;
  margin-bottom: 10vh;
  margin-left: auto;
  margin-right: auto;
}

.UploadImageMenu {
  position: fixed;
  top: 0;
  background-color: var(--dark-green);
  width: 100vw;
  height: 100vh;
  text-align: center;
}

.UploadContainer input {
  display: none;
}

.UploadContainer {
  background-color: white;
  color: var(--dark-green);
  font-size: 6vh;
  padding: 3vh;
  border-radius: 1.5vh;
  display: inline-block;
  min-width: 60vw;
}



